import React, { useState } from "react";
import {
	BrowserRouter as Router,
	Switch,
	Route,
} from "react-router-dom";

import {
	Home,
	GraphView,
} from "./pages";

import "./App.css";

const App = () => {
	const [graphData, setGraphData] = useState()
	console.log({
		graphData
	})


	return (
		<Router>
			<Switch>
				<Route path="/more-discourses/420"><h1>Blaze It Faggot</h1></Route>
				<Route path="/more-discourses/3d-graph"><GraphView graphData={graphData}/></Route>
				<Route path="/more-discourses"><Home graphData={graphData} setGraphData={setGraphData}/></Route>
			</Switch>
		</Router>

	)
};

export default App;